@function rem($value) {
  $remValue: ($value / 16) + rem;
  @return $remValue;
}

@mixin button {
  font-size: $standard-font-size;
  line-height: rem(27);
  border-radius: rem(4);
  padding: rem(15) rem(30);
  transition: $transition;
  transition: $transform;
  &:hover {
    transform: $scale;
  }
}

@mixin hero-hr {
    position: absolute;
    top: rem(-14);
    left: 0;
    right: 0;
    bottom: 0;
    background-repeat: no-repeat;
    background-position: calc(100% + 24px) 0;
    content: "";
    height: rem(24);
}